:root {
  --bg-light-mode-color: #fff;
  --bg-dark-mode-color: #21313c;
  --font-dark-mode-color: #fff;
}

html, body {
  height: 100%;
  font-family: Heebo;
  transition: background-color .5s;
}

span {
  white-space: pre;
}

th {
  color: #89989b;
  text-align: right;
  font-weight: bold;
}

td {
  padding-left: 20px;
}

main {
  box-sizing: border-box;
  flex-direction: column;
  height: 100%;
  padding: 1rem;
  display: flex;
}

#nav {
  color: #89989b;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-weight: bold;
  display: flex;
}

#nav button, #nav select {
  border-radius: 3px;
  font-size: 1.1rem;
  box-shadow: 0 1px 3px #0000001a, 0 1px 2px #0000000f;
}

.theme-label {
  padding-left: 10px;
  line-height: 35px;
}

.dark-mode {
  background-color: var(--bg-dark-mode-color);
  color: var(--font-dark-mode-color);
}

body span.theme-label:before {
  content: "‚òÄÔ∏è";
}

body.dark-mode span.theme-label:before {
  content: "üåô";
}

.body-content {
  display: flex;
}

#chart {
  border-top: none;
  flex-grow: 1;
  width: 80%;
  height: 100%;
}

.chartInfo {
  width: 520px;
  font-size: 20px;
}

.chart-title {
  text-align: center;
  color: #89989b;
}

#chartDataButton {
  width: 100%;
  margin-top: 1em;
  padding: .5rem 1rem;
}

#jsonViewer {
  visibility: hidden;
  --background-color: var(--bg-light-mode-color);
  --string-color: #00a35c;
  --preview-color: #ffc010;
  --property-color: #0498ec;
  --number-color: #b45af2;
  --boolean-color: #ef5752;
  transition: background-color .5s;
}

body.dark-mode #jsonViewer {
  --background-color: var(--bg-dark-mode-color);
  --string-color: #00ed64;
  --preview-color: #ffec9e;
}
